<template>
    <div>
        <!-- Nav Bar -->
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <a class="navbar-brand" href="#">AntGate</a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
            <ul class="navbar-nav">
            <li class="nav-item active">
                <a class="nav-link" href="#/">Home <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item active">
                <a class="nav-link" href="#/addCategory">Add Category <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item active">
                <a class="nav-link" href="#/category">Category <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item active">
                <a class="nav-link" href="#/books">Books <span class="sr-only">(current)</span></a>
            </li>
            </ul>
        </div>
        </nav>


        <!-- Body -->
        <form>
            <!-- Book Title -->
            <div class="col-auto mt-5">
            <label class="sr-only" for="bookCategoryName">NAME</label>
            <div class="input-group mb-2">
                <div class="input-group-prepend">
                <div class="input-group-text">NAME</div>
                </div>
                <input v-model="name" type="text" class="form-control" id="bookCategoryName" placeholder="Write Category Name">
            </div>
            </div>

            <!-- Book Body -->
            <div class="col-auto mt-5">
            <label class="sr-only" for="bookCategoryValue">VALUE</label>
            <div class="input-group mb-2">
                <div class="input-group-prepend">
                <div class="input-group-text">VALUE</div>
                </div>
                <input v-model="value" type="text" class="form-control" id="bookCategoryValue" placeholder="Write Category Value">
            </div>
            </div>
        </form>

        <!-- Button -->
        <button type="button" class="btn btn-primary mt-5 w-50 btn-upload" @click="upload">Upload</button>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return {
            name : null,
            value : null,
        }
    },
    methods : {
        async upload(){
            // Upload To Category Server
            const uploadData = {
                "name" : this.name,
                "value" : this.value,
            }

           await axios.post(`${this.$hostname}/createCategory`,uploadData);

           this.$router.push('/category')
        }
    }
}
</script>

